html body.bootstrapiso>:not(#wpwrap),
html body.bootstrapiso:not(#random_id_string) > {
	@import "bootstrap/dist/css/bootstrap";
}


html body.bootstrapiso > :not(#wpwrap), 
html body.bootstrapiso:not(#random_id_string) > { 
  .modal{
    overflow-y: auto !important;
    z-index: 100001 !important;
    max-width: none;
    background: transparent;
    padding: 0;
    border-radius: initial;
    box-shadow: none;	
  }
  
  .modal-backdrop.show{
    z-index: 100000 !important;
  }
}

.robo-newdialog-label-new{
	border-radius: 3px;
    display: inline;
    color: #fff;
    font-size: 0.70rem;
    line-height: 1.5;
    font-weight: bold;
    padding: 2px 5px;
    text-align: center;
    vertical-align: baseline;
    white-space: nowrap;
    background-color: #5cb85c !important;
    width: auto;
    position: absolute;
    right: -5px;
    top: -8px;
    font-size: 11px;
    font-family: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,Oxygen-Sans,Ubuntu,Cantarell,"Helvetica Neue",sans-serif;
}